


.overview {
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
    display: flex;
    color: #000;
    background: #ccc;
}
.overview>.overviewLeft{
    width: 40%;
    height: 100%;
    box-sizing: border-box;
    padding: 10px;
    background: #fff;
}
.overview>.overviewLeft>.notice{
    display: flex;
    justify-content: space-between;
}
.overview>.overviewLeft>.notice>p:nth-child(1){
    font-size: 20px;
    color: red;
}
.overview>.overviewLeft>.notice>p:nth-child(2){
    font-size: 20px;
    color: #000;
    font-weight: 600;
}
.overview>.overviewLeft ul>li{
    font-size: 14px;
    color: #777;
    line-height: 60px;
    list-style: disc;
    text-indent: 2em;
}
.overview>.overviewRight{
    width: 60%;
    height: 100%;
    background: skyblue;
    box-sizing: border-box;
    padding: 10px;
    background: #fff;
    margin-left:20px; 

}
.overview>.overviewRight>h2{
    font-size: 20px;
    color: red;
}
.overview>.overviewRight>.event{
    height: 1.5rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background: #fff;
}
.overview>.overviewRight>.event>li{
    width: 1rem;
    height: 0.5rem;
    text-align: center;
    line-height: 0.5rem;
    color: #fff;
}
.overview>.overviewRight>.event>li:nth-child(1){
    background: #6dc7be;
}
.overview>.overviewRight>.event>li:nth-child(2){
    background: #56bdde;
}
.overview>.overviewRight>.event>li:nth-child(3){
    background: #ff8762;
}
.overview>.overviewRight>.oa{
    display: flex;
    justify-content: space-around;
    height: 2rem;
    align-items: center;
}
.overview>.overviewRight>.oa>li{
    width: 0.6rem;
    height: 0.6rem;
    background: #6dc7be;
    text-align: center;
    line-height: 0.6rem;
    color: #fff;
    border-radius:50%; 
}
.overview>.overviewRight>.oa>li:nth-child(2){
    background: #ff8762;
}
.overview>.overviewRight>.oa>li:nth-child(3){
    background: #56bdde;
}

.overview>.overviewRight>.oa>li:nth-child(4){
    background: #b198dc;
}
